<template>
    <div class="login">
        <div class="out">
            <div class="logo">
                <img src="@/assets/images/logo_1.png" alt="">
            </div>
            <div class="inner">
                <el-form label-width="100px" :model="loginForm" :rules="loginRules" status-icon ref="loginForm">
                    <el-form-item label="" prop="username">
                        <el-input v-model="loginForm.username" placeholder="" clearable></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="password">
                        <el-input v-model="loginForm.password" type="password" placeholder="" clearable></el-input>
                    </el-form-item>
                    <el-form-item >
                        <el-button type="primary" @click="onSubmit"></el-button>
                        <el-button @click="onReset"></el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>
<script>
import { Form, FormItem, Input, Button ,Message} from 'element-ui'
import Vue from 'vue'
import { loginAxios } from '@/network/networks'
Vue.prototype.$message = Message
export default {
    data(){
        return{
            loginForm:{
                username:"",
                password:""
            },
            loginRules:{
                username:[
                    { required: true, message: '', trigger: 'blur' },
                    { min:5, max:15, message:'5-15', trigger:'blur'}
                ],
                password:[
                    { required: true, message: '', trigger: 'blur' },
                    { min:5, max:15, message:'5-15', trigger:'blur'}
                ]
            }
        }
    },
  components: {
    'el-form': Form,
    'el-form-item': FormItem,
    'el-input': Input,
    'el-button': Button
  }, 
  methods: {
      onSubmit(){
          this.$refs['loginForm'].validate((valid)=>{
              if(!valid){
                  this.$message.error('');
                  return
              }
            loginAxios(this.loginForm).then(res => {
                if(res.data.code == "401"){
                      this.$message.error(res.data.txt)
                  }else if(res.data.code == "200"){
                      window.localStorage.setItem('token',res.data.token)
                      this.$message.success("")
                      this.$router.push("/home")
                      
                  }
            })
          })
      },
      onReset(){
          this.$refs['loginForm'].resetFields();
      }
  },
}
</script>
