    height: 100%;
    background: #0d3f67;
    position: relative;
    .out{
        width: 460px;
        height: 360px;
        background: #fff;
        border-radius: 8px;
        box-shadow: 0 0 10px rgba(0,0,0,.3);
        position: absolute;
        top: 50%;
        left:50%;
        margin:-180px 0 0 -230px;
        text-align: center;
        .logo{
            width: 120px;
            height: 120px;
            background: #41c88c;
            border-radius: 50%;
            overflow: hidden;
            display: flex;
            justify-content: center;
            align-items: center;
            margin: -60px auto 0 auto;
            img{
                width: 70%;
            }
        }
    }
    .inner{
        min-height:200px;
        padding-top:40px;
        padding-right:40px;
    }
}
</style>
